/* ................profile.............. */
.profile-wrapper .col {
  padding: 0px;
  margin: 0;
}

@-webkit-keyframes easedown {
  0% {
    -webkit-transform: translateY(-5%);
            transform: translateY(-5%);
  }
  50% {
    -webkit-transform: translateY(-2%);
            transform: translateY(-2%);
  }
}

@keyframes easedown {
  0% {
    -webkit-transform: translateY(-5%);
            transform: translateY(-5%);
  }
  50% {
    -webkit-transform: translateY(-2%);
            transform: translateY(-2%);
  }
}

.profile-picture .image img {
  width: 180px;
  height: 180px;
  border-radius: 50%;
  border: 3px solid #fff;
  -webkit-animation: easedown 0.1s linear;
          animation: easedown 0.1s linear;
}

.profile-picture .image img:hover {
  -webkit-filter: grayscale(50%);
          filter: grayscale(50%);
}

.profile-picture .profile-about-cover {
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
}

.cover i {
  -webkit-transform: translateY(-130%);
          transform: translateY(-130%);
  float: right;
  color: white;
}

.profile-picture .image i {
  /* display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  transform: translateY(-140%); */
  position: absolute;
  left: 46%;
  top: 50%;
  display: none;
}
.profile-picture .image:hover i {
  display: block;
  cursor: pointer;
}
.tab-content-wrapper {
  width: 90%;
  margin: 0 auto;
}
.tab-content-wrapper .content {
  background-color: #fff;
}
.tabs-wrapper {
  display: flex;
}

.tabs-wrapper div {
  flex: 1 1;
  text-align: center;
  display: inline-block;
  font-weight: bold;
}
.profile-tabs ul li::after {
    content: '';
    display: block;
    width: 0;
    height: 2px;
    background: green;
    transition: width .2s;
}

.profile-tabs ul li:hover::after {
  width: 100%;
}
.profile-tabs ul li:hover {
    cursor: pointer;
}

.bio-wrapper {
  /* display: flex;
  justify-content: center;
  align-items: center; */
}

.bio span {
  display: block;
  padding: 30px 0px;
}

/*............image upload...............*/
.modal-wrapper {
  position: fixed; /* Stay in place */
  z-index: 1; /* Sit on top */
  padding-top: 100px; /* Location of the box */
  left: 0;
  top: 0;
  width: 100%; /* Full width */
  height: 100%; /* Full height */
  overflow: auto; /* Enable scroll if needed */
  background-color: rgb(0,0,0); /* Fallback color */
  background-color: rgba(0,0,0,0.7); /* Black w/ opacity */
}
.modal-wrapper .close i {
  position: relative;
  top: 7px;
}
.modal-wrapper .close:hover {
  color: grey;
  cursor: pointer
}
.image-modal {
  /* width: 40%;
  height: 50%; */
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  /* position: absolute;
  left: 30%; */
}
.image-modal .select-file {
  width: 300px;
}

.image-modal .select-file input {
  //transform: translateY(-70%);
  margin-bottom: 5px;
  display: inline-block;
  border: 1px solid #999;
  border-radius: 3px;
  padding: 5px 8px;
  outline: none;
  white-space: nowrap;
  -webkit-user-select: none;
  cursor: pointer;
  color: white;
  font-weight: 700;
  font-size: 10pt;
}
.image-modal .photo {
  max-width: 256px;
  max-height: 256px;
}
.image-modal .photo .responsive-img {
  max-width: 256px;
  max-height: 256px !important;
  overflow: auto;
}

